import back from '@/components/back'
import { setInterval, clearInterval } from 'timers';

export default {
    components:{
        back
    },
    data(){
        return {
			title: '新起点互联网医院处方笺',
            isEdit: false,
            obj: null,
            ListDetails: [],
            currentdate: '',
            info: '',
            mod: false,
            deadline: this.$store.state.effectiveTime,
            days: 1,
            Diagnoses: '',
            useDays:'',
            ValidDaysRemark: '',
            PrescriptionNo: '',
            MedicationCompanyId: '',
            MedicationCompanyName: '',
            orderInfo: this.$store.state.pInfo,
            user: this.$store.state.userInfo,
            preId: null,
            showConfirm: false,
            showConfirm2: false,
            popIdx: null,
            timer: null,
            o: null,
			diagnosesInput:false,
			diagnosesArr:[],
			inputFlag: false,
			isEditTitle:"编辑",
			noMedicine:false,
			noUsage:false,
			MedicationIdArr:[],
			pullStatus:false,
			item: this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.PatientSex:[],
			itemList:[['男','女']],
			itemShow: false,
			itemAge: [],
			itemAgeList:[[]],
			itemAgeShow: false,
			infoPatientName:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.infoPatientName:'',
			modifyPatientInfo:this.$store.state.modifyPatientInfo,
			MismatchShow:false,
			MismatchName:'',
			date: '',
			dataShow:false,
			currentdate:'',
			isYear:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.isYear:0,
			ageshow:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.age:'', 
			AgeYears:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.AgeYears:0,
			AgeMonths:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.AgeMonths:0,
			AgeDays:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.AgeDays:0,
			checkPre: {
				status: false,
				id: null,
				msg: ''
			}
        }
    },
    watch:{
        ListDetails(n){
            if(n.length){
                this.$refs.back.flag2 = false
            }else{
				this.$refs.back.flag2 = true
			}
		},
		
    },
	computed:{
	    totalPrice(){
	        let p = 0
	        if(this.ListDetails.length){
	            this.ListDetails.forEach(v => {
	                p += (+v.Price) * v.Quantity
	            })
	        }
	        return p.toFixed(2)
	    }
	},
    created(){
        const data = true
        this.$bridge.callhandler('backConfirm', data)
        this.timer = setInterval(() => {
            this.$bridge.registerhandler('confirmBack', (data, responseCallback) => {
                responseCallback('cantBack')
                this.$refs.back.back()
            })
        },50)
        this.getDate()
		this.pull()
		const {upList,Diagnoses2,saveFlag,pullFlag} = this.$store.state
        if(pullFlag == 0){
            this.getMPre()
        }
		if(pullFlag == 1){
			this.createPre()
		}
		this.ListDetails = upList
        this.Diagnoses = Diagnoses2
		
		for (var i = 14; i < 100; i++) {//1~99年龄循环
			this.itemAgeList[0].push(i+'岁')
		}	
    },
    mounted(){
        this.$refs.back.flag = false
        this.$refs.back.flag2 = true
		if(!this.$route.query.MPrescriptionId && !this.$route.query.rejectId && !this.$route.query.DataId){
			this.createPre()
		}
		this.diagnosesData()
		this.reSelection2()
		const {pullTemp} = this.$store.state
		if(pullTemp == 1){
			this.getTemporary()
		}
		//console.log(this.$refs.vuxPopupDialog)
		
    },
    beforeDestroy(){
        clearInterval(this.timer)
        this.$store.dispatch('pushDiagnoses2', this.Diagnoses)
    },
    methods:{
		infoPatientVal(){
			const inpEl = this.$refs.infoP
			inpEl.focus()
			  var length = inpEl.value.length
			  if (document.selection) {
				var range = inpEl.createTextRange()
				range.moveStart('character', length)
				range.collapse()
				range.select()
			  } else if (typeof inpEl.selectionStart === 'number' && typeof inpEl.selectionEnd === 'number') { 
				inpEl.selectionStart = inpEl.selectionEnd = length
			  }
		},
		goModifyPatientInfo(){
			let arr = {
				infoPatientName:this.infoPatientName,
				PatientSex:this.item,
				age:this.ageshow,
				isYear:this.isYear,
				AgeYears:this.AgeYears,
				AgeMonths:this.AgeMonths,
				AgeDays:this.AgeDays,
			}
			this.$store.dispatch('pushmodifyPatientInfo', arr)
		},
		pull(){
			 this.$post('InspectionAPI/Censorate/CensorateInit',{
			    PatientId:this.orderInfo.PatientInfo.PatientId
			}).then(o => {
				this.info = o
				if(!this.modifyPatientInfo){
					this.infoPatientName = o.PatientName
					this.item[0] = o.PatientSex == 1?'男':o.PatientSex == 2?'女':'未知'
					//o.PatientAge = ''
					//console.log(o.PatientAge)
					
					this.checkAge(o.PatientAge)
				}else{
					this.checkAge(this.ageshow)
				}
				this.$nextTick(() => {
					this.itemShow = true
					this.itemAgeShow = true
					setTimeout(()=>{
						this.itemShow = false
						this.itemAgeShow = false
					},10)
				})
			})
		},
		checkAge(val){
			if(!val){ //如果没有年龄不做判断
				return;
			}
			let age = val.split('岁')
			if(val.indexOf("岁") === -1 || Number(age[0]) < 1){ //小于1岁
				if(val.indexOf("个月") === -1){
					val = '0个月' + val
				}
				this.itemAge[0] = val
				this.ageshow = val
				this.AgeYears = 0
				let Months = val.split("个月")
				this.AgeMonths = Months[0] == 0 ? '' : Months[0]
				let Days = Months[1].split("天")
				this.AgeDays = Days[0]
			}else{
				this.itemAge[0] = age[0]+'岁'
				if(Number(age[0]) < 14){ //小于14岁
					this.isYear = 1; //按年月日
					this.AgeYears = age[0]
					if(val.indexOf("个月") != -1){
						let Months = age[1].split("个月")
						this.AgeMonths = Months[0]
						this.ageshow = age[0]+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeMonths = ''
						this.ageshow = age[0]+'岁'
					}
				}else{
					if(val.indexOf("个月") != -1){
						this.isYear = 1; //按年月日
						this.AgeYears = age[0]
						let Months = age[1].split("个月")
						this.AgeMonths = Months[0]
						this.ageshow = age[0]+'岁'+this.AgeMonths+'个月'
					}else{
						this.isYear = 0; //按年龄
						this.AgeYears = age[0]
						this.AgeMonths = ''
						this.ageshow = val
					}
				}
			}
			this.calculationData(val)
		},
		calculationData(val){
			let date = new Date();
			let year = Number(date.getFullYear());
			let month = Number(date.getMonth() + 1);
			let strDate = Number(date.getDate());
			let age = val.split('岁')
			if(val.indexOf("岁") === -1 || Number(age[0]) < 1){
				age[0] = 0
			}
			if(strDate - this.AgeDays < 0){   //天小于0
				if(month-this.AgeMonths < 1){  // 月小于0
					this.date = (year-1-age[0])+'-'+(month+12-1-this.AgeMonths)+'-'+(strDate+30-this.AgeDays)
				}else{
					this.date = (year-age[0])+'-'+(month-this.AgeMonths-1)+'-'+(strDate+30-this.AgeDays)
				}
			}else{  //天大于等于0
				if(month-this.AgeMonths < 1){  // 月小于0
					this.date = (year-1-age[0])+'-'+(month+12-this.AgeMonths)+'-'+(strDate-this.AgeDays)
				}else{
					this.date = (year-age[0])+'-'+(month-this.AgeMonths-1)+'-'+(strDate-this.AgeDays)
				}
			}
				//2个月2天  2018-11-14    2019-1-16
			console.log(this.date)
			console.log(this.AgeYears)
			console.log(this.AgeMonths)
			console.log(this.AgeDays)
		},
		onChange(val){
			let val_ = val.join('')
			let valAge = val.join('')
			if(val_.indexOf('岁') === -1 && (val_.indexOf('天') != -1 || val_.indexOf('月') != -1)){
				val_ = 1 + '岁'
				valAge = 1 + '岁'
			}
			this.ageshow = val_
			this.AgeYears = valAge.split('岁')[0]
			this.AgeMonths = 0
			this.AgeDays = 0
			console.log(this.AgeYears)
			console.log(this.AgeMonths)
			console.log(this.AgeDays)
		},
		changeData(val){
			let date = new Date();
			let year = Number(date.getFullYear());
			let month = Number(date.getMonth() + 1);
			let strDate = Number(date.getDate());
			let val_ = val.split('-')
			console.log(val)
			//1岁1个月29天  2019-2-29    2019-12-13
			if(strDate - Number(val_[2]) < 0){  //天
				if(month - Number(val_[1]) < 1){  //月
					this.AgeMonths = month + 12 - 1 - Number(val_[1])
					this.AgeDays = strDate + 30 - Number(val_[2]) 
					if(year - Number(val_[0]) === 0){
						this.AgeYears = 0
						this.AgeMonths = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) < 2){ //年
						this.AgeYears = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) <= 14){
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'
					}
				}else{
					this.AgeMonths = month - 1 - Number(val_[1])
					this.AgeDays = strDate + 30 - Number(val_[2]) 
					if(year - Number(val_[0]) === 0){
						this.AgeYears = 0
						//this.AgeMonths = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) <= 1){ //年
						this.AgeYears = 0
						if(this.AgeMonths == 0){
							this.AgeYears = year - Number(val_[0]) -1
							this.AgeMonths = 12
							this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
						}else{
							this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
						}
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'
					}
				}
			}else if(strDate - Number(val_[2]) === 0){
				if(month - Number(val_[1]) === 0){
					this.AgeMonths = month - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) <= 1){ //年
						this.AgeYears = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'
					}
				}else if(month - Number(val_[1]) < 1){  //月
					this.AgeMonths = month + 12  - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) <= 1){ //年
						this.AgeYears = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'
					}
				}else{//1岁1个月29天  2018-11-13    2019-12-13
					this.AgeMonths = month - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) <= 1){ //年
						this.AgeYears = 0
						if(this.AgeMonths == 0){
							this.AgeYears = year - Number(val_[0])
							this.AgeMonths = 12
							this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
						}else{
							this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
						}
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'
					}
				}

			}else{   //1岁1个月29天  2019-12-12    2019-12-13
				if(month - Number(val_[1]) === 0){
					this.AgeMonths = month - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) < 1){ //年
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'
					}
				}else if(month - Number(val_[1]) < 1){  //月
					this.AgeMonths = month + 12 - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) <= 1){ //年
						if(this.AgeMonths == 12){
							this.AgeYears = year - Number(val_[0])
							this.AgeMonths = 0
							this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
						}else{
							this.AgeYears = 0
							this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
						}
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'
					}
				}else{ //1岁1个月29天  20017-11-12    2019-12-13
					this.AgeMonths = month - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) < 1){ //年
						this.AgeYears = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'
					}
				}
			}
		},
		changeIsYear(){
			if(this.isYear){
				this.itemAgeShow = false
				this.dataShow = true
				setTimeout(()=>{
					document.getElementById("transfHtml").innerText = '按岁'
					document.getElementById("transf").style.display="flex";
					document.getElementById("transf").style.bottom="267px";
					document.getElementById("transf").style.width="90px";
					document.getElementById("transf").style.marginLeft="-45px";
				},300)
				
			}else{
				this.itemAgeShow = true
				this.dataShow = false
				setTimeout(()=>{
					document.getElementById("transfHtml").innerText = '14周岁以下请按年月日选择'
					document.getElementById("transf").style.display="flex";
					document.getElementById("transf").style.bottom="247px";
					document.getElementById("transf").style.width="210px";
					document.getElementById("transf").style.marginLeft="-105px";
				},300)
			}
		},
		transfHide(){
			document.getElementById("transf").style.display="none";
		},
		transfIsYear(){
			if(this.isYear){
				this.isYear = 0 
			}else{
				this.isYear = 1
			}
			this.changeIsYear()
		},
		addPharmacy(){
			this.goModifyPatientInfo()
			this.$store.dispatch('pushisShowXC',true)
			this.$store.dispatch('pushisUp',0)
			this.$store.dispatch('pushpullFlag', 1)
			this.$store.dispatch('pushpullTemp', 0)
			this.$router.push('/pharmacy?from=p')
		},
		deadlineChoose(i){
			if(this.mod){
				this.mod = false
			}else{
				this.mod = true
			}
			if(i == 2){
				this.$store.dispatch('pushEffectiveTime', this.deadline)
			}
		},
		isEditval(){
			if(this.isEdit){
				this.isEdit = false
				this.isEditTitle = "编辑"
			}else{
				this.isEdit = true
				this.isEditTitle = "保存"
			}
		},
		inputFor(){
			if(this.inputFlag){
			    return
			}
			if(this.Diagnoses == ""){
				this.diagnosesInput = true
			}else{
				this.diagnosesInput = false
			}
		},
		changeCount(n){
			event.preventDefault();
			let that = this;
			if(n == 2){
				if(this.Diagnoses != ""){
					setTimeout(function(){
						that.diagnosesInput = false
					},100)
				}
			}else{
				setTimeout(function(){
					that.diagnosesInput = false
				},100)
			}
		},
		diagnosesInputMethods(n,val){
			if(n == 2){
				this.$post('Prescription/Prescription/DelDiagnosesHistory',{
				    Id: val.Id
				}).then(o => {
				    this.diagnosesData()
				})
			}else{
				if(this.diagnosesArr.length != 0){
					this.diagnosesInput = true
				}
			}	
		},
		diagnosesData(){
			this.$get('Prescription/Prescription/GetDiagnosesHistoryList',{
			    DoctorId: this.user.Data.User.DoctorId,
			}).then(o => {
			    this.diagnosesArr = o
			})
		},
		diagnosesVal(val){
			this.Diagnoses = val.Diagnoses
		},
        operate(type, i, idx){
            if(type === 'del' && i.Quantity > 1){
                i.Quantity -= 1
            }
            else if(type === 'add'){
                i.Quantity = +i.Quantity + 1
            }
            this.ListDetails.splice(idx, 1, i)
        },
        temporary(){
            const List = []
            this.ListDetails.forEach(v => {
                List.push({
                    MedicationId: v.MedicationId,
                    UsageTimeId: v.UsageTimeId,
                    UsageTime: v.UsageTime,
                    UsageMethodId: v.UsageMethodId,
                    UsageMethod: v.UsageMethod,
                    DayDosageId: v.DayDosageId,
                    DayDosage: v.DayDosage,
                    Dosage: v.Dosage,
                    DosageUnitId: v.DosageUnitId,
                    DosageUnit: v.DosageUnit,
                    PackageUnitId: v.PackageUnitId,
                    PackageUnit: v.PackageUnit,
                    Quantity: v.Quantity,
                    Remark: v.Remark,
                    DosageDaysId: v.DosageDaysId,
                    DosageDays: v.DosageDays
                })
            })
            this.$post('Prescription/Prescription/SaveTempPrescription',{
                No: this.PrescriptionNo,
                PatientId: this.orderInfo.PatientInfo.PatientId,
                PatientName: this.orderInfo.PatientInfo.PatientName,
                ApplicantId: this.orderInfo.ApplicantId,
                ApplicantName: this.orderInfo.ApplicantName,
                OrgId: 0,
                DoctorId: this.user.Data.User.DoctorId,
                DoctorName: this.user.Data.User.DoctorName,
                Diagnoses: this.Diagnoses,
                ValidDays: this.deadline,
                ValidDaysRemark: this.ValidDaysRemark,
                DosageDays: this.days,
                DosageDaysRemark: this.useDays,
                Remark: '',
                MedicationCompanyId: this.MedicationCompanyId,
                ListDetails: List,
            }).then(o => {
                this.$toast('暂存成功')
                this.$store.dispatch('pushcMed', null)
                const now = location.href.split('?')[0]
                const start = this.$store.state.startUrl
                const data = '123'
                this.$bridge.callhandler('BackToIM', data)
				this.$store.dispatch('pushpullFlag', 0)
            })
        },
        getTemporary(){
            this.$post('Prescription/Prescription/GetTempPrescription',{
                PatientId: this.orderInfo.PatientInfo.PatientId,
                DoctorId: this.user.Data.User.DoctorId,
            }).then(o => {
                this.$store.dispatch('pushpullFlag', 1)
				this.$bridge.callhandler('printdata','初始进入1')
                if(o){
                    // this.o = o
					//let o = o
					this.$bridge.callhandler('printdata','初始进入2.5'+o)
					this.ListDetails = o.ListDetails ? o.ListDetails : []
					this.Diagnoses = o.Diagnoses
					this.deadline = o.ValidDays
					this.ValidDaysRemark = o.ValidDaysRemark
					this.DosageDays = o.days
					this.DosageDaysRemark = o.useDays
					this.$store.dispatch('pushupList', o.ListDetails ? o.ListDetails : [])
					this.$bridge.callhandler('printdata','初始进入2'+o.ListDetails.length)
					if(this.MedicationCompanyId !== o.MedicationCompanyId){
					    this.$post('Prescription/Pharmacy/SetMyMedicationCompany',{
					        MedicationCompanyId: o.MedicationCompanyId,
					        DoctorId: this.user.Data.User.DoctorId,
					    }).then(o => {
					        this.$post('Prescription/Prescription/GetNewPrescriptionBaseInfo',this.$pick({
					            ApplyId: this.orderInfo?this.orderInfo.Id : 0,
					            DoctorId: this.user.Data.User.DoctorId,
					            OrgId: -1,
					        })).then(o => {
					            if(o.IsExistPrescription == 1){
					                this.ListDetails = o.PrescriptionInfo.ListDetails
					                this.Diagnoses = o.PrescriptionInfo.Diagnoses
					                this.deadline = o.PrescriptionInfo.ValidDays
					                this.ValidDaysRemark = o.PrescriptionInfo.ValidDaysRemark
					                this.days = o.PrescriptionInfo.DosageDays
					                this.useDays = o.PrescriptionInfo.DosageDaysRemark
					                this.PrescriptionNo = o.PrescriptionInfo.No
					                this.MedicationCompanyId = o.PrescriptionInfo.MedicationCompanyId
					                this.MedicationCompanyName = o.PrescriptionInfo.MedicationCompanyName
					                this.preId = o.PrescriptionInfo.Id
					                this.$store.dispatch('pushpreNo',{
					                    PrescriptionNo: o.PrescriptionInfo.No,
					                    MedicationCompanyId: o.PrescriptionInfo.MedicationCompanyId,
					                    MedicationCompanyName: o.PrescriptionInfo.MedicationCompanyName
					                })
									this.$bridge.callhandler('printdata','初始进入3'+o.PrescriptionInfo.ListDetails.length)
					            }
					            else{
					                this.$store.dispatch('pushpreNo',o.InitInfo)
					                this.PrescriptionNo = o.InitInfo.PrescriptionNo
					                this.MedicationCompanyId = o.InitInfo.MedicationCompanyId
					                this.MedicationCompanyName = o.InitInfo.MedicationCompanyName
					            }
								this.$store.dispatch('pushpullTemp', 0)
					        })
					    })
					}
                }
            })
			this.$bridge.callhandler('printdata','初始进入4'+this.ListDetails)
        },
        confirm2(){
            
        },
        getDate(){
            var date = new Date();
            var seperator1 = "-";
            var year = date.getFullYear();
            var month = date.getMonth() + 1;
            var strDate = date.getDate();
            if (month >= 1 && month <= 9) {
                month = "0" + month;
            }
            if (strDate >= 0 && strDate <= 9) {
                strDate = "0" + strDate;
            }
            this.currentdate = year + seperator1 + month + seperator1 + strDate;
        },
        createPre(){
            const o = this.$store.state.preNo
            if(o){
                this.PrescriptionNo = o.PrescriptionNo
                this.MedicationCompanyId = o.MedicationCompanyId
                this.MedicationCompanyName = o.MedicationCompanyName
            }
            else{
                this.$post('Prescription/Prescription/GetNewPrescriptionBaseInfo',this.$pick({
                    ApplyId: this.orderInfo?this.orderInfo.Id : 0,
                    DoctorId: this.user.Data.User.DoctorId,
                    OrgId: -1,
                })).then(o => {
                    if(o.IsExistPrescription == 1){
                        this.ListDetails = o.PrescriptionInfo.ListDetails
                        this.Diagnoses = o.PrescriptionInfo.Diagnoses
                        this.deadline = o.PrescriptionInfo.ValidDays
                        this.ValidDaysRemark = o.PrescriptionInfo.ValidDaysRemark
                        this.days = o.PrescriptionInfo.DosageDays
                        this.useDays = o.PrescriptionInfo.DosageDaysRemark
                        this.PrescriptionNo = o.PrescriptionInfo.No
                        this.MedicationCompanyId = o.PrescriptionInfo.MedicationCompanyId
                        this.MedicationCompanyName = o.PrescriptionInfo.MedicationCompanyName
                        this.preId = o.PrescriptionInfo.Id
                        this.$store.dispatch('pushpreNo',{
                            PrescriptionNo: o.PrescriptionInfo.No,
                            MedicationCompanyId: o.PrescriptionInfo.MedicationCompanyId,
                            MedicationCompanyName: o.PrescriptionInfo.MedicationCompanyName
                        })
						this.$bridge.callhandler('printdata',this.MedicationCompanyId+'id1') 
                    }
                    else{
                        this.$store.dispatch('pushpreNo',o.InitInfo)
                        this.PrescriptionNo = o.InitInfo.PrescriptionNo
                        this.MedicationCompanyId = o.InitInfo.MedicationCompanyId
                        this.MedicationCompanyName = o.InitInfo.MedicationCompanyName
						this.$bridge.callhandler('printdata',this.MedicationCompanyId+'id2') 
						if(!this.$route.query.MPrescriptionId && !this.$route.query.rejectId && !this.$route.query.DataId){
							this.ListDetails.forEach(v => {
							    this.MedicationIdArr.push(v.MedicationId)
							})
							if(this.$route.query.MedicationCompanyId != null && this.$route.query.MedicationCompanyId > 0){
								this.getMedicationStatus(this.$route.query.MedicationCompanyId,this.MedicationIdArr)
								this.setMyMedicationCompany(this.$route.query.MedicationCompanyId,true)
								this.MedicationCompanyId = this.$route.query.MedicationCompanyId
								this.$store.dispatch('pushpreNo',{
								    MedicationCompanyId: this.$route.query.MedicationCompanyId,
									PrescriptionNo:o.InitInfo.PrescriptionNo
								})
							}
						}
						if(this.MedicationIdArr.length > 0 && (this.$route.query.MPrescriptionId || this.$route.query.rejectId || this.$route.query.DataId)){
							this.getMedicationStatus(this.MedicationCompanyId,this.MedicationIdArr)
						}
                    }
                })
            }
        },
		  save(){ //保存常用处方
			if(!this.ListDetails.length){
				return this.$toast('请添加药品')
			}
			if(!this.Diagnoses){
				return this.$toast('请填写临床诊断')
			}
			if(!this.ListDetails){
				return this.$toast('请选择药品')
			}
			const list = []
			this.ListDetails.forEach(v => {
				const obj = {}
				obj.MedicationId = v.MedicationId
				obj.UsageTimeId = v.UsageTimeId
				obj.UsageTime = v.UsageTime
				obj.UsageMethodId = v.UsageMethodId
				obj.UsageMethod = v.UsageMethod
				obj.PackageUnitId = v.PackageUnitId
				obj.PackageUnit = v.PackageUnit
				obj.DayDosageId = v.DayDosageId
				obj.DayDosage = v.DayDosage
				obj.Dosage = v.Dosage
				obj.DosageUnitId = v.DosageUnitId
				obj.DosageUnit = v.DosageUnit
				obj.Quantity = v.Quantity
				obj.Remark = v.Remark 
				obj.DosageDaysId= v.DosageDaysId,
				obj.DosageDays= v.DosageDays
				list.push(obj)
			})
			var id = ''
			if(this.$store.state.primaryId){
				id = this.$store.state.primaryId
			}else if(this.$store.state.PhaId){
				id = this.$store.state.PhaId
			}else{
				id = ''
			}
			this.$post('Prescription/Prescription/SavePrescriptionOftenUsed', this.$pick({
				id: id,
				DoctorId:this.user.Data.User.DoctorId,
				OrgId: this.user.Data.User.OrgId,
				Diagnoses: this.Diagnoses,
				MedicationCompanyId:this.MedicationCompanyId,
				ListDetails: list
			})).then(o => {
				this.$store.dispatch('pushPhaId', o)
				this.$toast('已存为常用处方！')
			})
		},
        pop(idx){
            this.showConfirm = true
            this.popIdx = idx
        },
        confirm(){
            this.ListDetails.splice(this.popIdx, 1)
        },
        modify(idx){
            this.$store.dispatch('pushcMed', this.ListDetails[idx])
            this.$router.push(`/modifyCon?idx=${idx}&&back=-1`)
        },
		reSelection(i,idx){
			this.$store.dispatch('pushsearchText',i.Name)
			this.$router.push('/medicine-list?id='+''+'&&from=p')
			const upListNum = {
				num:this.ListDetails.length,
				id:i.MedicationId
			}
			this.$store.dispatch('pushupListNum', upListNum) 
			
		},
		reSelection2(){
			if(this.$store.state.upListNum.num && this.ListDetails.length >= this.$store.state.upListNum.num){
				this.ListDetails.forEach((v,k) => {
					if(!v.MedicationStatus){
						this.ListDetails[k].MedicationStatus = 0	
					}
				})
			}
		},
        getMPre(){
            if(this.$route.query.MPrescriptionId && this.$route.query.MPrescriptionId != 0){
                this.$post('Prescription/Prescription/GetPrescription',{
                    Id: this.$route.query.MPrescriptionId
                }).then(o => {
					this.$bridge.callhandler('printdata',o+'data')
					this.$bridge.callhandler('printdata',new Date()+'time')
					o.ListDetails.forEach(v => {
					    this.MedicationIdArr.push(v.MedicationId)
					})
					this.$bridge.callhandler('printdata',this.MedicationIdArr+'arr2')
					this.$bridge.callhandler('printdata',o.MedicationCompanyId+'id')
					this.ListDetails = o.ListDetails
					//this.MedicationCompanyId = o.MedicationCompanyId
					this.setMyMedicationCompany(o.MedicationCompanyId,true)							
					this.Diagnoses = o.Diagnoses
                })
            }
            if(this.$route.query.rejectId && this.$route.query.rejectId != 0){
                this.$post('Prescription/Prescription/GetPrescription',{
                    Id: this.$route.query.rejectId
                }).then(o => {
					o.ListDetails.forEach(v => {
					    this.MedicationIdArr.push(v.MedicationId)
					})
					this.ListDetails = o.ListDetails
					//this.MedicationCompanyId = o.MedicationCompanyId
					this.setMyMedicationCompany(o.MedicationCompanyId,true)
                    this.Diagnoses = o.Diagnoses
                })
			}
			if(this.$route.query.DataSource && this.$route.query.DataId){
				let arr = {
					DataSource:this.$route.query.DataSource,
					DataId:this.$route.query.DataId
				}
				this.$store.dispatch('pushdataSource2',arr)

				this.$post('Prescription/Prescription/GetPrescriptionInitInfo',{
					DataSource: this.$route.query.DataSource, //0：新开处方，1：经典处方，2：历史处方，3，常用处方，4：续方，5：母处方直接开方，6：药品需求单
					DataId: this.$route.query.DataId,
					PatientId: this.orderInfo.PatientInfo.PatientId,
					DoctorId: this.user.Data.User.DoctorId
                }).then(o => {
					if(o.ResultInfo.ErrorCode != 0){ //需求单状态弹层
						this.checkPre.msg = o.ResultInfo.ErrorMessage;
						this.checkPre.id = o.ResultInfo.DataId;
						this.checkPre.status = true;
					}else{
						o.MedicationDetails.forEach(v => {
							this.MedicationIdArr.push(v.MedicationId)
						})
						this.ListDetails = o.MedicationDetails
						//this.MedicationCompanyId = o.MedicationCompanyId
						this.setMyMedicationCompany(this.ListDetails[0].MedicationCompanyId,false)
						this.Diagnoses = o.BaseInfo.Diagnoses
					}
                })
			}
        },
		getMedicationStatus(a,b){
			const ListDetails = this.ListDetails
			this.$post('Prescription/Medication/GetMedicationStatus',{
			    MedicationCompanyId: a,
				MedicationIdList:b
			}).then(o => {
				ListDetails.forEach(v => {
				    o.forEach(a => {
				        if(v.MedicationId === a.MedicationId){
				            v.MedicationStatus = a.MedicationStatus
							v.MedicationStock = a.MedicationStock
				        }
						if(a.MedicationStatus != 0){
							this.noMedicine = true
							this.noMedicine = false
						}
				    })
				})
			})
			this.ListDetails = ListDetails
			this.$store.dispatch('pushupList',ListDetails)
			this.$store.dispatch('pushpullFlag', 1)
		},
		setMyMedicationCompany(a,status){
			this.$post('Prescription/Pharmacy/SwitchMyMedicationCompany',{
			    MedicationCompanyId: a,
			    DoctorId: this.user.Data.User.DoctorId,
				MedicationType:1,	
				OrgId:this.user.Data.User.OrgId,
			}).then(o => {
				if(o.Code == 2 && status){
					this.MismatchShow = true
					this.MismatchName = o.Message
				}
				this.createPre()
			})
		},	
		MismatchShowConfirm(){
			this.MismatchShow = false
		},
		noMedicineConfirm(){
			this.noMedicine = false
		},
        send(){
            if(!(this.ListDetails && this.ListDetails.length)){
                return this.$toast('请添加药品')
			}
            if(!this.Diagnoses){
                return this.$toast('请填写临床诊断')
            }
            if(!this.deadline){
                return this.$toast('请填写处方笺有效期')
            }
			if(this.mod){
				return this.$toast('请先保存处方有效期')
			}
			if(this.isEdit){
				return this.$toast('请先保存处方笺')
			}
			if(!this.infoPatientName){
				return this.$toast('请填写姓名')
			}
			if(this.item[0] != '男' && this.item[0] != '女'){
				return this.$toast('请选择性别')
			}
			if(!this.ageshow){
				return this.$toast('请选择年龄')
			}
			
			for(let i in this.ListDetails){
				if(!this.ListDetails[i].DayDosage || (this.ListDetails[i].DosageUnit!='适量' && !this.ListDetails[i].Dosage) || !this.ListDetails[i].DosageUnit || !this.ListDetails[i].UsageMethod || !this.ListDetails[i].Quantity || !this.ListDetails[i].PackageUnit){
					this.noUsage = true;
					return;
				}
			}
            const List = []
            this.ListDetails.forEach(v => {
                List.push({
                    MedicationId: v.MedicationId || v.Id,
                    UsageTimeId: v.UsageTimeId,
                    UsageTime: v.UsageTime,
                    UsageMethodId: v.UsageMethodId,
                    UsageMethod: v.UsageMethod,
                    DayDosageId: v.DayDosageId,
                    DayDosage: v.DayDosage,
                    Dosage: v.Dosage,
                    DosageUnitId: v.DosageUnitId,
                    DosageUnit: v.DosageUnit,
                    PackageUnitId: v.PackageUnitId,
                    PackageUnit: v.PackageUnit,
                    Quantity: v.Quantity,
                    Remark: v.Remark,
                    DosageDaysId: v.DosageDaysId,
                    DosageDays: v.DosageDays,
					MedicationCompanyId:v.MedicationCompanyId
                })
            })
            const ApplyId = this.orderInfo && this.orderInfo.Method ? this.orderInfo.Id : null
			const MPrescriptionId = this.orderInfo && this.orderInfo.PrescriptionId ? this.orderInfo.PrescriptionId : this.$route.query.MPrescriptionId
			const DataSource = this.$store.state.dataSource2.DataSource ? this.$store.state.dataSource2.DataSource : null
			const DataId = this.$store.state.dataSource2.DataId ? this.$store.state.dataSource2.DataId : null
            this.$post('Prescription/Prescription/SavePrescription',this.$pick({
                Id: this.preId,
                MPrescriptionId,
                ApplyId,
                PatientId: this.orderInfo.PatientInfo.PatientId,
                PatientName: this.infoPatientName,
				PatientSex:this.item[0]=='男'?1:this.item[0]=='女'?2:0,
				AgeYears:this.AgeYears,
				AgeMonths:this.AgeMonths,
				AgeDays:this.AgeDays,
                ApplicantId: this.orderInfo.ApplicantId,
                ApplicantName: this.orderInfo.ApplicantName,
                OrgId: 0,
                DoctorId: this.user.Data.User.DoctorId,
                DoctorName: this.user.Data.User.DoctorName,
                Diagnoses: this.Diagnoses,
                ValidDays: this.deadline,
                ValidDaysRemark: this.ValidDaysRemark,
                DosageDays: this.days,
                DosageDaysRemark: this.useDays,
                Remark: '',
                MedicationCompanyId: this.MedicationCompanyId,
                ListDetails: List,
				No: this.PrescriptionNo,
				DataSource,
				DataId,
            })).then(o => {
				if(o.Code == 2){
					const ListDetails = this.ListDetails
					ListDetails.forEach(v => {
					    o.Data.forEach(a => {
					        if(v.MedicationId === a.MedicationId){
					            v.MedicationStatus = a.MedicationStatus
								v.MedicationStock = a.MedicationStock
					        }
					    })
					})
					this.ListDetails = ListDetails
					this.$store.dispatch('pushupList',ListDetails)
					this.$store.dispatch('pushpullFlag', 1)
					this.noMedicine = true
				}else{
					if(o.IsNeedSign){
						let parama = {
							UniqueId:o.UniqueId,
							PrescriptionId:o.PrescriptionId,
							MedicationType:1
						}
					    this.$bridge.callhandler('signId',JSON.stringify(parama) , (data) => {
					        this.$router.replace(`/success?name=${this.infoPatientName}&NO=${this.PrescriptionNo}`)
					    })
					}
					else{
					    this.$router.replace(`/success?name=${this.infoPatientName}&NO=${this.PrescriptionNo}`)
					}
				}
					
                
            })
		},
		preMaskSub(status,id){ //需求单状态弹层
			if(status){
				this.checkPre.status = false;
				this.$store.dispatch('pushBackIM', 1);
				this.$router.replace('/PrescriptionLlist?id='+ id); //跳转到处方单详情
			}else{
				this.checkPre.status = false;
				const data = '123'
                this.$bridge.callhandler('BackToIM', data); //返回IM
			}
		}
    }
}



